* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.App {
    width: 100%;
    padding: 30px;
}

.App-Content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.Blog-Avatar {
    margin-top: 52px;
    border-radius: 50%;
    border: 1px solid #262424;
}

.Blog-UserName {
    margin-top: 16px;
    font-size: 20pt;
    font-family: monospace;
}

.Blog-Profile {
    margin-top: 16px;
}

.Blog-Contact {
    margin-top: 16px;

}

.Blog-Contact .Blog-Link {
    text-decoration: none;
    color: darksalmon;
}

.Blog-Sub-Btn {
    background-color: #fff;
    margin-top: 16px;
    padding: 8px;
    border: 1px solid black;
    border-radius: 20px;
}

.Blog-Sub-Btn:hover {
    background-color: #f5f1f1;
    transition: background-color 0.3s;
}

.Blog-ContentDelim {
    margin-top: 32px;
    width: 200px;
    border-top: 1px solid #bdbdbd;
}

.Blog-Email {
    margin-top: 48px;
    font-weight: 500;
}

.Blog-Socials {
    margin-top: 16px;
    display: flex;
    flex-direction: row;
}

.Blog-Socials-Ico {
    margin-left: 6px;
    justify-content: space-between;
}